
.wrapper {
  height: 96.8vh;
  border-radius: 6px;
  flex-direction: column;
  text-align: center;
}

.wrapper,
.testUlList {
  display: flex;
  align-items: center;
}

.header,
.escription {
  font-family: 'Helvetica Neue', sans-serif;
  span {
    display: block;
    padding: 10px;
  }
}

.tested-word {
  display: block;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 28px;
  font-weight: 500;
  padding: 50px;

  &::after {
    height: 1px;
    width: 200%;
    background-color: blueviolet;
    display: block;
    content: '';
    margin-left: -50%;
  }
}

.testUlList {
  justify-content: center;
  flex-wrap: wrap;

  li {
    opacity: 1;
    transition: 1s all ease;
  }
}


.listElement {
  margin: 15px;
  opacity: 0;

  span {
    display: block;
    cursor: pointer;
    background-color: #f1f1f1;
    padding: 10px 5px;
    min-width: 40px;
    text-align: center;
    border-radius: 15px;
    border: 2px solid green;
    width: fit-content;

    &:active {
      background-color: black;
      color: #fff;
      border-color: greenyellow;
    }
  }
}
